.travels__content {
  padding-top: 120px;
  padding-bottom: 120px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1160px;

  @media (min-width: $min-tablet-width) and (max-width: $max-tablet-width) {
    padding-top: 100px;
    padding-bottom: 63px;
    max-width: 678px;
    align-items: unset;
  }

  @media (max-width: $max-mobile-width) {
    flex-wrap: wrap;
    padding: 40px 15px 57px;
  }
}

.travels__text {
  width: 530px;

  @media (min-width: 1024px) and (max-width: 1300px) {
    padding-right: 50px;
  }

  @media (min-width: $min-tablet-width) and (max-width: $max-tablet-width) {
    width: 354px;
  }

  h2 {
    @include font($font-28, $line-height-42, $semi-bold);
    margin: 0;
    margin-top: 16px;

    @media (min-width: $min-tablet-width) and (max-width: $max-tablet-width) {
      @include font($font-26, $line-height-36, $semi-bold);
      padding-right: 115px;
      margin-top: -8px;
    }

    @media (max-width: $max-mobile-width) {
      @include font($font-24, $line-height-34, $semi-bold);
    }
  }

  p {
    margin-top: 22px;

    @media (min-width: $min-tablet-width) and (max-width: $max-tablet-width) {
      padding-right: 29px;
      margin-top: 16px;
    }

    @media (max-width: $max-mobile-width) {
      margin-top: 16px;
      margin-bottom: 34px;
    }
  }
}

.travels__img {
  height: 301px;

  @media (min-width: $min-tablet-width) and (max-width: $max-tablet-width) {
    width: 324px;
  }

  @media (max-width: $max-mobile-width) {
    margin: 0 auto;
    width: 100%;
    height: 100%;
  }

  img {
    @media (min-width: $min-tablet-width) and (max-width: $max-tablet-width) {
      width: 324px;
      height: 264px;
    }

    @media (max-width: $max-mobile-width) {
      width: 100%;
      height: 100%;
    }
  }
}
